<template>
    <div>
        <Loader />
        <div class="d-block header-section">
            <div class="float-left">
                <h1>
                    <b>Manufacturer</b>
                </h1>
            </div>
        </div>
        <div class="container-fluid productManagement">
            <!-- START: Card Data-->
            <div class="row">
                <div class="col-12 mt-3">
                    <div class="card">
                        <div class="card-header justify-content-between align-items-center">
                            <h4 class="card-title pl-3 m-2">
                                <img src="@/assets/images/icons/manufacture.png" /> Manufacturer Details
                            </h4>
                        </div>
                        <div class="card-body">
                            <div class="col-md-12 form-row mr-5 ml-5">
                                <div class="col-md-3">
                                    <p class="mb-0 text-muted">Manufacturer Name</p>
                                    <p class="mb-0 text-primary">{{manufacturerDto.name}}</p>
                                </div>
                                <div class="col-md-3">
                                    <p class="mb-0 text-muted">Excise Licence Number</p>
                                    <p class="mb-0 text-primary">{{manufacturerDto.exciseLicenseNumber}}</p>
                                </div>
                                <div class="col-md-3">
                                    <p class="mb-0 text-muted">TIN Number</p>
                                    <p class="mb-0 text-primary">{{manufacturerDto.tinNumber}}</p>
                                </div>
                            </div>
                            <hr class="dot mr-5 ml-5" />
                            <div class="col-md-12 form-row mr-5 ml-5">
                                <div class="col-md-3">
                                    <p class="mb-0 text-muted">Contact Name</p>
                                    <p class="mb-0 text-primary">{{manufacturerDto.contact.name}}</p>
                                </div>
                                <div class="col-md-3">
                                    <p class="mb-0 text-muted">Email ID</p>
                                    <p class="mb-0 text-primary">{{manufacturerDto.contact.email}}</p>
                                </div>
                                <div class="col-md-3">
                                    <p class="mb-0 text-muted">Contact Number</p>
                                    <p class="mb-0 text-primary">{{manufacturerDto.contact.countryCode}} {{manufacturerDto.contact.phone}}</p>
                                </div>
                            </div>
                            <hr class="dot mr-5 ml-5" />
                            <div class="col-md-12 form-row mr-5 ml-5">
                                <div class="col-md-3">
                                    <p class="mb-0 text-muted">Address</p>
                                    <p class="mb-0 text-primary">{{manufacturerDto.address.description}}</p>
                                </div>
                                <div class="col-md-3">
                                    <p class="mb-0 text-muted">Country</p>
                                    <p class="mb-0 text-primary">{{manufacturerDto.address.country.name}}</p>
                                </div>
                                <div class="col-md-3">
                                    <p class="mb-0 text-muted">Region</p>
                                    <p class="mb-0 text-primary">{{manufacturerDto.address.region.name}}</p>
                                </div>
                                <div class="col-md-3">
                                    <p class="mb-0 text-muted">Postal Code</p>
                                    <p class="mb-0 text-primary">{{manufacturerDto.address.postalAddress}}</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-12 mt-3">
                    <div class="card">
                        <div class="card-header justify-content-between align-items-center">
                            <h4 class="card-title pl-3 m-2">
                                <img src="@/assets/images/icons/product.png" /> Product(s)
                            </h4>
                        </div>
                        <div class="card-body">
                            <div class="col-md-12 form-row mr-5 ml-5">
                                <div class="col-md-6" v-if="manufacturerDto.products.filter(element=>element.product.origin===2).length>0">
                                    <p class="mb-0 text-muted">Imported</p>
                                    <div class="media d-flex border col-md-2 float-left mr-2 p-2"
                                         v-for="(prod,index) in manufacturerDto.products.filter(element=>element.product.origin===2)"
                                         :key="index">
                                        <div class="media-body align-self-center pl-2">
                                            <center>
                                                <b class="mb-0">
                                                    <img src="@/assets/images/icons/tobacco-imported.png" />
                                                    <br />
                                                    {{prod.product.name}}
                                                </b>
                                            </center>
                                        </div>
                                    </div>
                                </div>
                                <div class="col-md-6" v-if="manufacturerDto.products.filter(element=>element.product.origin===1).length>0">
                                    <p class="mb-0 text-muted">Domestic</p>
                                    <div class="media d-flex border col-md-2 float-left mr-2 p-2"
                                         v-for="(prod,index) in manufacturerDto.products.filter(element=>element.product.origin===1)"
                                         :key="index">
                                        <div class="media-body align-self-center pl-2">
                                            <center>
                                                <b class="mb-0">
                                                    <img src="@/assets/images/icons/alcohol-domestic.png" />
                                                    <br />
                                                    {{prod.product.name}}
                                                </b>
                                                <br />
                                            </center>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div class="col-12 mt-3">
                    <div class="card">
                        <div class="card-header justify-content-between align-items-center">
                            <h4 class="card-title pl-3 m-2">
                                <img src="@/assets/images/icons/pro.png" /> Product Name(s)
                            </h4>
                        </div>
                        <div class="card-body" v-if="manufacturerDto.brandProducts.length>0">
                            <div class="col-12 col-sm-11 mt-3 ml-5">
                                <div class="table-responsive">
                                    <table class="table layout-secondary bordered">
                                        <thead>
                                            <tr>
                                                <th>Product Name</th>
                                                <th>Product Origin</th>
                                                <th>Product type</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-for="(bProd,index) in manufacturerDto.brandProducts" :key="index">
                                                <td>{{bProd.name}}</td>
                                                <td class="text-danger" v-if="bProd.product.origin===2">Imported</td>
                                                <td class="text-success" v-else>Domestic</td>
                                                <td>
                                                    <a href="#">
                                                        <img src="@/assets/images/icons/tobacco-imported.png" />
                                                        {{bProd.product.name}}
                                                    </a>
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div class="col-12 mt-3" v-if="manufacturerDto.suppliers.length>0">
                    <div class="card">
                        <div class="card-header justify-content-between align-items-center">
                            <h4 class="card-title pl-3 m-2">
                                <img src="@/assets/images/icons/supplier.png" /> Supplier(s)
                            </h4>
                        </div>
                        <div class="card-body" v-for="(supp,index) in manufacturerDto.suppliers" :key="index">
                            <div class="col-md-12 form-row mr-5 ml-5">
                                <div class="col-md-3">
                                    <p class="mb-0 text-muted">Supplier Name</p>
                                    <p class="mb-0 text-primary">{{supp.supplier.name}}</p>
                                </div>
                                <div class="col-md-3">
                                    <p class="mb-0 text-muted">TIN Number</p>
                                    <p class="mb-0 text-primary">{{manufacturerDto.tinNumber}}</p>
                                </div>
                                <div class="col-md-3">
                                    <p class="mb-0 text-muted">Address</p>
                                    <p class="mb-0 text-primary">{{supp.supplier.address.description}}</p>
                                </div>
                            </div>
                            <hr class="dot mr-5 ml-5" />
                            <div class="col-md-12 form-row mr-5 ml-5">
                                <div class="col-md-3">
                                    <p class="mb-0 text-muted">Email ID</p>
                                    <p class="mb-0 text-primary">{{supp.supplier.contact.email}}</p>
                                </div>
                                <div class="col-md-3">
                                    <p class="mb-0 text-muted">Country</p>
                                    <p class="mb-0 text-primary">{{supp.supplier.address.country.name}}</p>
                                </div>
                                <div class="col-md-3">
                                    <p class="mb-0 text-muted">Postal Code</p>
                                    <p class="mb-0 text-primary">{{supp.supplier.address.postalAddress}}</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div class="col-12 mt-3" v-if="manufacturerDto.warehouses.length>0">
                    <div class="card">
                        <div class="card-header justify-content-between align-items-center">
                            <h4 class="card-title pl-3 m-2">
                                <img src="@/assets/images/icons/warehouse.png" /> Warehouse
                            </h4>
                        </div>
                        <div class="card-body"
                             v-for="(location,index) in manufacturerDto.warehouses"
                             :key="index">
                            <div class="col-md-12 form-row mr-5 ml-5">
                                <div class="col-md-12">
                                    <p class="mb-0 text-muted">Location Name</p>
                                    <p class="mb-0 text-primary">{{location.warehouse.name}}</p>
                                </div>
                            </div>
                            <hr class="dot mr-5 ml-5" />
                            <div class="col-md-12 form-row mr-5 ml-5">
                                <div class="col-md-3">
                                    <p class="mb-0 text-muted">Name</p>
                                    <p class="mb-0 text-primary">{{location.warehouse.contact.name}}</p>
                                </div>
                                <div class="col-md-3">
                                    <p class="mb-0 text-muted">Email ID</p>
                                    <p class="mb-0 text-primary">{{location.warehouse.contact.email}}</p>
                                </div>
                                <div class="col-md-3">
                                    <p class="mb-0 text-muted">Phone Number</p>
                                    <p class="mb-0 text-primary">{{location.warehouse.contact.countryCode}} {{location.warehouse.contact.phone}}</p>
                                </div>
                            </div>
                            <hr class="dot mr-5 ml-5" />
                            <div class="col-md-12 form-row mr-5 ml-5">
                                <div class="col-md-3">
                                    <p class="mb-0 text-muted">Address</p>
                                    <p class="mb-0 text-primary">{{location.warehouse.address.description}}</p>
                                </div>
                                <div class="col-md-3">
                                    <p class="mb-0 text-muted">Region</p>
                                    <p class="mb-0 text-primary">{{location.warehouse.address.region.name}}</p>
                                </div>
                                <div class="col-md-3">
                                    <p class="mb-0 text-muted">Postal Code</p>
                                    <p class="mb-0 text-primary">{{location.warehouse.address.postalAddress}}</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div class="col-12 mt-3">
                    <div class="card">
                        <div class="card-header justify-content-between align-items-center">
                            <h4 class="card-title pl-3 m-2">
                                <img src="@/assets/images/icons/documents.png" /> Documents
                            </h4>
                        </div>
                        <div class="card-body" v-if="manufacturerDto.uploadedFiles.length>0">
                            <div v-for="(file,index) in manufacturerDto.uploadedFiles" :key="index">
                                <div class="col-md-12 form-row mr-5 ml-5">
                                    <div class="col-md-2">
                                        <p class="mt-1 text-muted">
                                            <img src="@/assets/images/icons/pdf.png" class="mr-2" />
                                            {{file.fileName}}
                                        </p>
                                    </div>
                                    <div class="col-md-2">
                                        <p class="mt-3 text-muted" v-if="file.fileSize < 1024 * 1024" >{{(((file.fileSize)/1024)).toFixed(2)}} KB</p>
                                        <p class="mt-3 text-muted" v-else>{{(((file.fileSize)/1024)/1024).toFixed(2)}} MB</p>
                                    </div>
                                    <div class="col-md-6">
                                        <p class="mt-3 text-muted">{{file.createdDateTime | FormatingDate}}</p>
                                    </div>
                                    <!-- <div class="col-md-1">
                                                        <p class="mt-1 text-muted">
                                                            <img src="@/assets/images/icons/cross.png" />
                                                        </p>
                                    </div>-->
                                    <div class="col-md-1">
                                        <p class="mt-1 text-muted">
                                            <img type="buttton"
                                                 v-on:click="downloadFile(file.fileURL,file.fileName)"
                                                 src="@/assets/images/icons/pdf.png" />
                                        </p>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="card-body" v-else>No documents..</div>
                    </div>
                </div>

                <div class="col-12 mt-3 mb-5">
                    <div class="card">
                        <div class="card-header justify-content-between align-items-center">
                            <h4 class="card-title pl-3 m-2">
                                <img src="@/assets/images/icons/comments.png" /> Comments
                            </h4>
                        </div>
                        <div class="card-body">
                            <div class="col-12 col-sm-12 mt-3">
                                <div v-if="manufacturerDto.approvals!==null">
                                    <p class="text-muted mb-2">History</p>
                                    <div class="table-responsive">
                                        <table class="table layout-secondary bordered">
                                            <thead>
                                                <tr>
                                                    <th>User</th>
                                                    <th>Action</th>
                                                    <th>ActionBy</th>
                                                    <th>Comment</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <template v-for="(approval) in manufacturerDto.approvals">
                                                    <tr v-for="(History,index) in approval.approvalHistories" :key="index">
                                                       <td v-if="approval.approver===1">GRA</td>
                                                        <td v-else>TSSP</td>
                                                        <td>{{History.action}}</td>
                                                        <td v-if="History.actionedBy==='Manufacturer'">{{manufacturerDto.name}}</td>
                                                        <td v-else>{{History.actionedBy}}</td>
                                                        <td>{{History.comment}}</td>
                                                    </tr>
                                                </template>
                                            </tbody>
                                        </table>
                                    </div>
                                </div>
                                <div v-else-if="manufacturerDto.comments.length>0">
                                    <p class="text-muted mb-2">Comments</p>
                                    <textarea class="form-control"
                                              rows="5"
                                              v-model="manufacturerDto.comments"
                                              disabled></textarea>
                                </div>
                                <div v-else><p class="text-muted mb-2">No comments..</p></div>
                            </div>
                            <button class="btn btn-primary pl-5 mr-2 pr-5 p-3 mr-5"
                                    style="float:right;"
                                    type="button"
                                    v-on:click="submitManufacturerDto()">
                                Submit
                            </button>
                            <button class="btn btn-outline-primary mr-2 p-3 pl-5 pr-5"
                                    style="float:right;"
                                    type="button"
                                    v-on:click="editManufacturerDto()">
                                Edit
                            </button>
                        </div>
                    </div>
                </div>
            </div>
            <!-- END: Card DATA-->
        </div>
    </div>
</template>

<script>
    import Loader from "../popup/loader";
    import axios from "axios";
    import toast from '@/toast';
    export default {
        data() {
            return {
            };
        },
        computed: {
            manufacturerDto(){
                return this.$store.getters.manufacturerDto;
            }
        },
        filters: {
            FormatingDate: function (value) {
                if (value == null || value == "") return "";
                var date = new Date(value);
                var months = ["Jan","Feb","Mar","Apr","May","Jun","Jul","Aug","Sep","Oct","Nov","Dec"];
                return ('0' + date.getDate()).slice(-2) + "-" + months[date.getMonth()]  + "-" + date.getFullYear();
            },
        },
        watch: {
        },
        components: {
            Loader,
        },
        methods: {
            editManufacturerDto() {
                this.$router.push( {name: 'RegisterWithSavedData', params:{Guid : this.$store.getters.manufacturerDto.mfGuid}});
            },
            downloadFile(fileNameWithURL, fileName) {
                var downloadURl=process.env.VUE_APP_VmApisBaseUrl+'/Storage/DownloadFile?fileNameWithURL='+fileNameWithURL;
                axios
                    .get(downloadURl,{responseType: 'arraybuffer'})
                    .then((resp) => {
                        if (resp.type != "text/plain") {
                           var blob = new Blob([resp.data],{type: "application/octet-stream"});
                            let saveAs = require("file-saver");
                            saveAs(blob, fileName);
                        } else toast.error("File not found in Server!");
                    });
            },
            async submitManufacturerDto() {
                this.runLoader(5000);
                await this.$store.dispatch("addManufacturerDtoAsync",this.$store.getters.manufacturerDto);
                if(this.$store.getters.manufacturerDto.mfId!==0){
                    toast.success("Manufacturer Submitted!..");
                    this.$router.push({name: 'successfullyregistered'});
                }
            },
            runLoader(sec) {
                document.getElementById("loader").style.display = "block";
                setInterval(() => {
                    document.getElementById("loader").style.display = "none";
                }, sec);
            }
        }
    };
</script>